.App {
	height: 100vh;
}

.newNote-container {
  margin: 0 auto;
  width: 50%;
  margin-top: 125px;
  height: 100vh;
  overflow: hidden; 
}

.newNote-container h1 {
  padding: 10px 0;
}

.btn {
  width: 100%;
}

.new-note-btn {
	width: inherit;
	background-color: #007bff;
	padding: 6px 12px;
	border: 1px solid #007bff;
	border-radius: 5px;
	color: white;
	text-decoration: none;
	transition: all 200ms;
}

textarea {
	width: 100%;
	border: none;
	outline: none;
}

.new-note-btn:hover {
	background-color: #0169d9;
	border: 1px solif #0169d9;
	color: white;
	text-decoration: none;
}
.alert {
  margin-top: 16px;
}

.notes-container {
	margin: 0 auto;
	width: 50%;
    margin-top: 125px;
}

.jumbo {
	margin: 0 auto;
	width: 50%;
    margin-top: 125px;
}

.notes-container h1 {
  margin: 0;
}

.pill {
	display: inline;
	margin: 0 16px;
}

.counter {
	position: relative;
	bottom: 4px;
	font-size: 30px;
	margin: 10px 0px;
}

.heading {
	display: flex;
	margin: 0;
	align-items: center;
	justify-content: space-between;
}

.new-note-btn2 {
	width: inherit;
	float: right;
	background-color: #007bff;
	padding: 6px 12px;
	border: 1px solid #007bff;
	border-radius: 5px;
	color: white;
	text-decoration: none;
	transition: all 200ms;
}

.new-note-btn2:hover {
	background-color: #0169d9;
	border: 1px solif #0169d9;
	color: white;
	text-decoration: none;
}

.heading h2 {
	margin: 0;
}

h2 {
	margin: 0 16px;
}

#remove {
	color: red;
	float: right;
	display: flex;
	position: relative;
	top: 2px;
	font-size: 20px;
	align-items: center;
	z-index: 1;
}

.name {
	text-align: left;
	margin-right: 0;
}

.links a {
	color: white;
	padding: 0 15px;
	text-decoration: none;
}

.links a:hover {
	color: white;
	text-decoration: none;
}

.notes {
	margin: 16px 0;

}

@media only screen and (max-width: 992px) {
	.links a {
		padding: 5px 0;
		display: flex;
	}

	.newNote-container, .notes-container, .jumbo {
		width: 80%;
	}
}